// ================= //
// === DARK MODE === //
// ================= //

// === VARIABLES === //

/* FONT-SIZE */
$base-font: 14px;

/* FONT-FAMILY */
$hfont: 'Fira Code', monospace; 
$bfont: 'Poppins', Arial, sans-serif; 

/* FONT-STYLE */
$bold: 700;
$light: 300;
$regular: 400;

/* FONT-COLOR */
$dark-body-color: #f0e2fd;
$dark-link-color: #d3e7f6;
$dark-link-hover: #fdc887;

/* BACKGROUND-COLOR */
$dark-bgcolor: #635892;
$dark-shadow: #534a7a;


// === DECLARATIONS === //

// FONTS //
body {
    background: $dark-bgcolor;
    font-family: $bfont;
    color: $dark-body-color;
    background-size: cover;
    height: 95vh;
}
.profile-img {
    border-radius: 200px;
    width: 85%;
    padding: 10px;
    box-shadow: 5px 7px 5px $dark-shadow;
}

.App-header {
    display: flex;
    justify-content: flex-start;

    h1 {
        font-size: $base-font * 3.5;
        font-family: $hfont;
        font-weight: $bold;
        line-height: $base-font * 2 ;
    }
    h2 {
        font-size: $base-font * 2;
        font-family: $hfont;
        font-weight: $regular;
    }
    .subtitle {
        font-size: $base-font * 1.5;
        font-family: $bfont;
        font-weight: $regular;
    }
}

/* ===  CONTAINERS ===  */

/* ABSOLUTE CENTER */
@mixin center($horizontal: true, $vertical: true) {
    position: absolute;
    // border: 1px solid chartreuse;
    @if ($horizontal and $vertical) {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    } @else if ($horizontal) {
      left: 50%;
      transform: translate(-50%, 0);
    } @else if ($vertical) {
      top: 50%;
      transform: translate(0, -50%);
    }
  }

  .center-container {
    // border: 1px solid chartreuse;
    position: relative;
    width: 95%;
    height: 95vh;
    margin: 20px auto;
  }
  
  .center-content {
    height: auto;
    width: 75%;
    padding: 20px;
    &.both {
      @include center;
    }
  
    &.horizontal {
      @include center(true, false);
    }
  
    &.vertical {
      @include center(false, true);
    }
  }

  @media only screen and (max-width: 600px) {
   .center-container{
        width:  100%;
   }
   .Landing {
       h1 {
           font-size: $base-font * 2;
       }
   }
  }
