
/* FONT-SIZE */
$base-font: 14px;

/* FONT-FAMILY */
$hfont: 'Fira Code', monospace; 
$bfont: 'Poppins', Arial, sans-serif; 

/* FONT-STYLE */
$bold: 700;
$light: 300;
$regular: 400;

/* FONT-COLOR */
$dark-body-color: #f0e2fd;
$dark-link-color: #d3e7f6;
$dark-link-hover: #fdc887;

/* BACKGROUND-COLOR */
$dark-bgcolor: #635892;
$dark-shadow: #534a7a;

/* BUTTON STYLES */
$button-radius: 15px;
$button-bgcolor: rgba(74,66,109, 0.7);
$button-bgcolor-hover:  rgba(74,66,109, 1);

.landing-menu{
    button {
        font-size: $base-font;
        font-family: $hfont;
        font-weight: $bold;
        padding: 15px 20px;
        margin: 10px 15px 10px 0;
        border-radius: $button-radius;
        color: $dark-body-color;
        background-color: $button-bgcolor;
        border: 3px solid $button-bgcolor;
        box-shadow: 5px 5px $button-bgcolor-hover;
        transition: ease-in-out 0.3s;
    }
    button:hover {
        background-color: $button-bgcolor-hover;
        border: 3px solid $dark-link-color;
        transition: ease-in-out 0.3s;
        transform: scale(1.2);
        cursor: pointer;
    }
}